@charset "UTF-8";

@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-style: normal;
}

*{
    padding: 0;
    margin: 0;

    font-family: 'Pretendard-Regular';
    box-sizing: border-box;
}

a{
    text-decoration: none;    
    color: inherit;
}


li{
    list-style: none;
}


.header{

margin-bottom: 40px;
}

.header p{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 10px;
    color:white;
}

.header span{
    display: block;
    text-align: right;
    color: white;
}


.inner {
    max-width: 640px;
    width: calc(100% - 40px);
    margin: 0 auto;
    position: relative;
    background-color: deepskyblue;
    padding: 50px;
    border-radius: 30px;
    margin-top: 100px;
}
.check form{
    position: relative;
    margin-bottom: 30px;
}

.check form input[type="text"]{
    width: 100%;
    height :70px;
    padding: 10px 20px;
    font-size: 20px;
    border-radius: 20px;
    border: 0;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.39);
    
}

.check form button{
   position: absolute;
   right: 20px;
   top: 50%;
   width: 8%;
   transform: translateY(-50%);
   background-color: transparent;
   border: 0;
}

.item{
    background-color: rgb(255, 216, 108);
    color: rgb(37, 37, 37);
    border-radius: 20px;
    margin-bottom: 30px;
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    position: relative;
    transition: 0.3s;
    cursor: pointer;    
    font-size: 20px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.39);
}



.item button{
    width: 50px;
    border: 0;
    background-color: transparent;
    
}
.item button img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: transparent;
}

.item strong, img{
    position: relative;
    z-index: 1;
    transition: 0.3s;
}

